<template>
  <div>
    <div class="form-container" @submit.prevent="iisSubmitted = true">
      <!-- Form formation -->
      <form class="card-formation">
        <div class="card-info">
          <label for="card-number">CARD NUMBER</label>
          <input
            type="text"
            class="form-control"
            id="card-number"
            placeholder="XXXX XXXX XXXX XXXX"
            maxlength="19"
            v-model.trim="usrObject.cardNumber"
          />
        </div>
        <div class="card-info">
          <label for="name">CARDHOLDER NAME</label>
          <input
            type="text"
            class="form-control"
            id="name"
            placeholder="FirstName LastName"
            v-model.trim="usrObject.cardHolder"
          />
        </div>

        <div class="card-validation-info">
          <div class="validation-info-month-year">
            <label for="date">EXP. MONTH</label>
            <select
              class="validity"
              name="month"
              id="months"
              v-model.trim="usrObject.expireMonth"
            >
              <option
                v-for="month in months"
                :key="month"
                :value="month.toString().length < 2 ? '0' + month : month"
              >
                {{ month }}
              </option>
            </select>
          </div>
          <div class="validation-info-month-year">
            <label for="date">EXP. YEAR</label>
            <select
              class="validity"
              name="year"
              id="years"
              v-model.trim="usrObject.expireYear"
            >
              <option
                v-for="year in years"
                :key="year"
                :value="year.toString().substr(2)"
              >
                {{ year }}
              </option>
            </select>
          </div>
        </div>

        <div class="card-info">
          <label for="select-name">VENDOR</label>

          <select class="select-vendor" v-model="usrObject.vendor">
            <option value="bitcoin">BITCOIN INC</option>
            <option value="ninja">NINJA BANK</option>
            <option value="blockchain">BLOCK CHAIN INC</option>
            <option value="evil">EVIL CORP</option>
          </select>
        </div>

        <Button @send="saveData" />
      </form>
    </div>
  </div>
</template>

<script>
import Button from "./Button.vue";
export default {
  name:'Form',
  props: { usrObject: Object },

  data() {
    return {
     
      months: 12,
      years: [2022, 2023, 2024, 2025, 2026],
      isSubmitted: false,
    };
  },

  components: { Button },

  methods: {
    saveData() {
      this.$emit("save");
    },
  },
};
</script>

<style scoped>
.form-container {
  padding-bottom: 24px;
}
.form-container,
.card-info {
  width: 382px;

  display: flex;
  flex-direction: column;
}

form input[type="text"] {
  margin-top: 5px;
  font-family: "PT Mono", monospace;
  border: solid 1px #000;
}

.form-control {
  width: 374px;
  height: 50px;
  border-radius: 8px;
}

.card-info {
  margin: 14px 0px;
}

.card-info,
.card-validation-info label {
  font-size: 12px;
  font-family: "PT Mono", monospace;
  font-weight: 400;
  text-align: start;
}
.card-info select {
  margin-top: 5px;
}

.card-validation-info {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 25px;
}
.validation-info-month-year {
  display: block;
}

.validity {
  width: 175px;
  height: 56px;
  border-radius: 8px;
}

.select-vendor {
  width: 374px;
  height: 56px;
  border-radius: 8px;
}

.select-vendor > option {
  width: 368px;
  background-color: #000;
  color: #fff;
  border-radius: 5px;
}
</style>
