<template>
  <div class="card-box">
    <div
      class="card-list"
      v-for="(obj,index) in cardArray"
      :key="index"
      :obj="obj"
      @click="cardActive(obj)">
      <img class="wifi" src="../assets/wifi_white.svg" alt="wifi" />
      <img class="chip" src="../assets/chip.svg" alt="chip" />

      <img
        class="logo"
        v-if="obj.vendor"
        :src="require(`../assets/${obj.vendor}.svg`)"
        alt="obj.vendor"
      />

      <p class="card-number">{{ obj.cardNumber }}</p>

      <p class="label-name">CARDHOLDER NAME</p>
      <p class="name">{{ obj.cardHolder }}</p>

      <p class="label-date">VALID THRU</p>
      <div class="card-validity-date">
        <p>{{ obj.expireMonth }}</p>
        <p>/</p>
        <p>{{ obj.expireYear }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name:'CardList',
  props: { cardArray: Array },
  methods:{
    cardActive(obj){
      this.$emit('choose' ,obj)
    }
  }
  
};
</script>

