<template>
  <div id="app">
    <Home
      :cardInfo="newUsers"
      :activeCardData="retrieveActiveCard"
      @chooseCard="getActiveCard"
    />
    <AddCard :cardUser="user" @store="getData" />
  </div>
</template>

<script>
import Home from "./views/Home.vue";
import AddCard from "./views/AddCard.vue";

export default {
  name: "app",
  data() {
    return {
      // isTrue: true,
      // Data storage to render card's list
      newUsers: [],
      // Data storage from where it's sent to localStorage
      sendActiveCard: [],
      // Data storage after retrive from localStorage
      retrieveActiveCard: "",
      // Data init state
      user: {
        cardNumber: "",
        cardholder: "",
        expireMonth: "",
        expireYear: "",
        CCV: "",
        vendor: {
          bitcoin: { name: "BITCOIN INC", bgColor: "#FFAE34" },
          ninja: { name: "NINJA BANK", bgColor: "#222222" },
          blockChain: { name: "BOLCK CHAIN INC", bgColor: "#8B58F9" },
          evilCorp: { name: "EVIL CORP", bgColor: "#F33355" },
        },
      },
    };
  },
  components: {
    AddCard,
    Home,
  },
  methods: {
    getData(data) {
      this.newUsers.push(data);
    },

    getActiveCard(data) {
      if (this.sendActiveCard.length > 0) {
        setTimeout(() => {
          this.sendActiveCard.push(data);
        }, 100);
        this.sendActiveCard.pop();
      } else {
        this.sendActiveCard.push(data);
      }
    },
  },

  watch: {
    sendActiveCard: {
      handler() {
        localStorage.setItem("card", JSON.stringify(this.sendActiveCard));
      },
      deep: true,
    },
  },

  created() {
    if (localStorage.getItem("card")) {
      const data = JSON.parse(localStorage.getItem("card"));
      this.retrieveActiveCard = data[0];

      // console.log(this.retrieveActiveCard);
      //  localStorage.clear()
    }
  },
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css2?family=PT+Mono&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;600;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
#app {
  display: flex;
  justify-content: space-around;
  margin-top: 30px;
}
</style>