<template>
  <div class="add-card">
    <Header :title="title" :sub="subtitle" />
    <CardDemo :usr="cardUser" />
    <Form @save="passData" :usrObject="cardUser" />
  </div>
</template>

<script>
import Header from "../components/Header.vue";
import CardDemo from "../components/CardDemo.vue";
import Form from "../components/Form.vue";

export default {
  name:'AddCard',
  props: { cardUser: Object },
  data() {
    return {
      title: "ADD A NEW BANK CARD",
      subtitle: "NEW CARD",
    };
  },
  components: { Form, CardDemo, Header },

  methods: {
    passData() {
      this.$emit("store", { ...this.cardUser });
    },
  },
};
</script>

<style  scoped>
.add-card {
  width: 416px;
  height: 896px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: -3px 4px 33px 18px rgba(0, 0, 0, 0.1);
  
}
</style>
